body{
    margin: 0;
}

.memory-bg{
    background-image:url('http://localhost:3000/assets/img/lake.jpg') ;
    background-size: cover;
    height: 100vh;

    
}

.grid-card{
    display: grid;
    grid-template-columns: repeat(4, 150px);
    grid-template-rows: repeat(3, 150px);
    grid-gap: 10px;

}
.card {
    // width: calc(100% * (1 / 4) - 10px - 1px);
    // width: auto;
    // margin: 5px;
    background-color: white;
    border: none;
    // height: 100%;
    // border-radius: 3px;
    box-shadow: 0 6px 11px -2px rgba(0, 0, 0, 0.54);
    transform-style: preserve-3d;
    overflow: hidden;
    transition: .5s ease-in-out all;
    
    img {
        transition: .2s ease-in-out all;
        opacity: 0;
    }
    
    &.-isFlipped{
        transition: .5s ease-in-out all;
        transform: rotateY(180deg);
        img {
            transition: .5s ease-in-out all;
            opacity: 1;
        }

    }

}
